ing: border-box;\n\t\t\t\tmin-height: calc(var(--default-grid-baseline) * 2 + var(--default-clickable-area));\n\t\t\t}\n\t\t}\n\t}\n\n\t.systemtags-picker__tag-color button {\n\t\tmargin-inline-start: calc(var(--default-grid-baseline) * 2);\n\n\t\tspan.pencil-icon {\n\t\t\tdisplay: none;\n\t\t\tcolor: var(--color-main-text);\n\t\t}\n\n\t\t&:focus,\n\t\t&:hover,\n\t\t&[aria-expanded='true'] {\n\t\t\t.pencil-icon {\n\t\t\t\tdisplay: block;\n\t\t\t}\n\t\t\t.circle-icon,\n\t\t\t.circle-outline-icon {\n\t\t\t\tdisplay: none;\n\t\t\t}\n\t\t}\n\t}\n\n\t.systemtags-picker__tag-create {\n\t\t:deep(span) {\n\t\t\ttext-align: start;\n\t\t}\n\t\t&-subline {\n\t\t\tfont-weight: normal;\n\t\t}\n\t}\n}\n\n.systemtags-picker__note {\n\tbottom: 0;\n\tpadding-block: 8px;\n\n\t:deep(.notecard) {\n\t\t// min 2 lines of text to avoid jumping\n\t\tmin-height: 2lh;\n\t\talign-items: center;\n\t}\n\n\t& > div {\n\t\tmargin: 0 !important;\n\t}\n}\n\n.systemtags-picker--done :deep(.empty-content__icon) {\n\topacity:  1;\n}\n\n// Rendered chip in note\n.nc-chip {\n\tdisplay: inline !important;\n}\n"],"sourceRoot":""}]);
